.html__body {
  position: relative;
  font-family: 'Castoro', serif;
  font-style: italic;
  color: #42412a;
  background-image: url("../../img/mramor.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-color: #ffffff;
  height: 100vh;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.strelka-left-5 {
  position: absolute;
  top: 50%;
  transform: translateY(-50%) rotate(90deg);
  width: 30px;
  height: 30px;
  fill: #42412a;
}
