.main__text {
  position: relative;
  text-align: center;
  font-size: 40px;
  margin: 0;
  padding: 40px;

  &::after {
    content: "Нажми сюда";
    display: block;
    position: absolute;
    font-size: 20px;
    top: 40px;
    right: 143px;
    width: 100px;
    height: 20px;
    font-family: 'Cinzel', serif;
  }
}

.main__span-link {
  font-family: 'Cinzel', serif;
  color: #42412a;
  text-decoration: none;
  border-bottom: 1px solid#42412a;
  font-style: normal;

  &:hover {
    color: gold;
    border-bottom: 1px solid gold;
  }
}

.main__head {
  position: relative;
  margin-top: 20px;
  margin-bottom: 75px;
  width: 100%;
  padding-top: 40px;
  line-height: 50px;
  text-align: center;
  font-size: 170px;
  font-weight: 400;
  font-family: 'Castoro', serif;
  font-style: normal;
  background-clip: text;
  color: transparent;
  background-image: linear-gradient(to bottom, #BF953F, #FCF6BA, #B38728, #FBF5B7, #AA771C);

  &::before {
    content: "";
    position: absolute;
    top: -30px;
    left: 200px;
    width: 790px;
    height: 240px;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 10px 10px 5px #9E9E9E;
    display: block;
    z-index: -1;
  }
}

.main__info-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.main__info {
  width: 50%;
  margin-bottom: 25px;
}

.main__ya-map {
  width: 50%;
}

.main__span {
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  font-size: 36px;
  letter-spacing: 3.1em;
  padding-left: 110px;
}

.main__contact {
  font-size: 40px;
  text-align: center;
}

.main__contact-tel {
  color: #42412a;
  text-decoration: none;
  border-bottom: 1px solid #42412a;
  margin-right: 20px;

  &:hover {
    color: gold;
    border-bottom: 1px solid gold;
  }
}

.main__span-time {
  display: block;
  margin-top: 11px;
}

.main__map {
  font-size: 40px;
  text-align: center;
  margin-top: 20px;
}
